.icon-toggler {
  display: none;
}
#navigation label {
  display: none;
}

#hamburger-icon{
  display: none;
}
  
@media screen and (max-width: 768px){
  #navigation label {
    display: inline-block;
    color: #fff;
    background: transparent;
    font-style: normal;
    font-size: 1.2em;
    padding: 10px;
  }

  #navigation ul {
    display: none;
  }

  #navigation ul li {
    display: block;
  }
  #navigation ul li {
    border-top: 1px solid transparent;
  }

  #navigation input:checked ~ ul {
    display: block;
  }

  #navigation{
    width: 10%;
  }
}

